import React, {useEffect} from "react"
import 'bootstrap/dist/css/bootstrap.min.css'
import { Container } from 'react-bootstrap';
import { Link } from "gatsby";
import { Helmet } from "react-helmet";
import { Image } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { Navbar } from "react-bootstrap";
import { Collapse } from "react-bootstrap";
import { Row, Col, Button } from "react-bootstrap";

import newsletter from "../../static/newsletter2(1).png"
import firstimg from "../../static/Mrs. Rajamani Teacher Photo 2019-2020.png"
import secondimg from "../../static/2020 Portrait.jpg"
import DDTVimg from "../../static/index.jpg"

var latestLink = "https://www.canva.com/design/DAEB5NGHJ5Q/f8YFMnbBYUdyZROJJxH4Ng/view?utm_content=DAEB5NGHJ5Q&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var firstLink = "https://www.canva.com/design/DAEDHBfSKfc/TZ7XXdSnt8WFeM0SMWKkcQ/view?utm_content=DAEDHBfSKfc&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var DDTVint = "https://www.canva.com/design/DAEDY1sY2HY/24LpLTpkIRhKnRb-xjgIZg/view?utm_content=DAEDY1sY2HY&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var eventLink = "https://www.canva.com/design/DAEDY1sY2HY/24LpLTpkIRhKnRb-xjgIZg/view?utm_content=DAEDY1sY2HY&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var newsTitle = "Learning From Afar"
var newsDesc = "A dive into the impact of distance learning on the education system"


export default function Home() {
  useEffect(() => {
    window.onscroll = function() {
      var navlinks = document.getElementsByClassName('navlink')
      if(window.pageYOffset >= 75) {
        document.getElementById('navbar').style.backgroundColor = "rgba(40,50,70,255)";
        for (var i=0;i<navlinks.length;i++) {
          navlinks[i].classList.add("thisHover")
          navlinks[i].classList.remove("hovering")
        }
      }
      else {
        document.getElementById('navbar').style.backgroundColor = "rgba(0.3,0.3,0.3,0.5)";
        for (var p=0;p<navlinks.length;p++) {
          navlinks[p].classList.add("hovering")
          navlinks[p].classList.remove("thisHover")
        }
      }
    };
  }, []);

  function openLink(e) {
    window.open(e)
  }

  return (
  <Container fluid>
    <Helmet>
        <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0, 
        height=device-height, target-densitydpi=device-dpi, shrink-to-fit=no, user-scalable=no" />
        <title>Events - BAYouth</title>
        <body id="events-page" />
    </Helmet>
    <Navbar collapseOnSelect expand="lg" fixed="top" id="navbar" style={{ backgroundColor: `rgba(0.3,0.3,0.3,0.5)` }}>
        <Navbar.Brand href="/" style={{ paddingRight: `1.5rem`, color: `white` }}>BAYouth</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link href="/articles" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Our Articles</Nav.Link>
                <Nav.Link href="/events" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Events</Nav.Link>
            </Nav>
            <Nav>
                <Nav.Link href="/meetus" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Meet the Team</Nav.Link>
            </Nav>
        </Navbar.Collapse>
    </Navbar>

    <div id="events-body">
      <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{ padding: 0, margin: `3.5rem 0` }} className="center overlay">
          </Col>
      </Row>
    </div>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Upcoming Events</p>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: 0, padding: 0 }}>
      <Col xs={12} sm={12} md={12} lg={12} xl={12} className="descCol showcase showCol2" style={{ margin: 0, padding: 0 }}>
        <div className="imgdiv imgborder showcase2 eventblock" /*onClick={() => openLink(DDTVint)}*/ style={{ margin: `15px 0 0 0` }}>
            <div className="imgdiv showcaseimg" style={{ height: `auto` }}>
                <Image className="image" src="https://manager.mosyle.com/images/cupertino-logo-h.png" fluid/>
            </div>
            <div className="desDiv">
                <div>
                <h3 className="desc">CUSD Candidates Forum</h3>
                <p className="desc"><i>Hosted By <a href="http://svcu4all.org" target="_blank">SVCU Youth Group</a></i></p>
                <p className="descText">In the upcoming CUSD election, four candidates are vying for
                two open seats on the board. The candidates are <b><a href="http://sites.google.com/view/wil4schools-org/" target="_blank">Wil Fluewelling</a></b>,{' '}
                <b><a href="https://www.sudhakasamsetty.org/" target="_blank">Sudha Kasamsetty</a></b>,{' '}
                <b><a href="https://www.sylvia4cusd.com/" target="_blank">Sylvia Leong</a></b>, and{' '}
                <b><a href="https://phyllisvogel.com/" target="_blank">Phyllis Vogel</a></b>. On Sept. 26, from 7 p.m. to 7:30 p.m., there will be a virtual forum for the community
                to get to know these candidates, and have them answer any questions. This forum is hosted by Silicon Valley Community United (SVCU) Youth Group. The zoom link will 
                be posted here soon.</p>
                </div>
            </div>
        </div>
        {/* <div className="secondArticle overlay hoverGray" style={{ margin: 0, left: 0 }} onClick={() => openLink(DDTVint)}></div> */}
      </Col>
    </Row>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Past Events</p>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: 0, padding: 0 }}>
      <Col xs={12} sm={12} md={12} lg={12} xl={12} className="descCol showcase showCol2" style={{ margin: 0, padding: 0 }}>
        <div className="imgdiv imgborder showcase2 eventblock" onClick={() => openLink(DDTVint)} style={{ margin: `15px 0 0 0` }}>
            <div className="imgdiv showcaseimg" style={{ height: `auto` }}>
                <Image className="image" src={DDTVimg} fluid/>
            </div>
            <div className="desDiv">
                <div>
                <h3 className="desc">Ding Ding TV Training</h3>
                <p className="desc"><i>Hosted By Ding Ding TV</i></p>
                <p className="descText">Ding Ding TV is the first Silicon Valley Chinese TV channel, founded by TV personality Diana Ding. 
                Ding Ding TV caters to audiences in both the US and China, with its interviews and videos reaching millions of people. 
                Ding Ding TV has hosted celebrities such as YouTube Co-Founder Steve Chen and Apple Co-Founder Steve Wozniak. 
                The organization has also won multiple awards including "Best TV Show" and "Most Distinguished TV Show" from the Northern California Chinese Media Association. 
                Ding Ding TV has graciously given three classes to the editors of this newsletter, on how best to conduct an interview, with Ms. Ding's expertise and experience.</p>
                </div>
            </div>
        </div>
        <div className="secondArticle overlay hoverGray" style={{ margin: 0, left: 0 }} onClick={() => openLink(DDTVint)}></div>
      </Col>
    </Row>

  </Container>
  )}